import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
  name: 'matchesType'
})
export class MatchTypePipe implements PipeTransform {
    transform(posts: Array<any>, type: string): Array<any> {
        if ( type != 'all')  
            return posts.filter(post => post.type === type);
        else 
            return posts;
    }
}   


